<template>
    <div id="login">
        <div class="container-fluid top-bg">
            <div class="top-box">
                <span class="top-title">|ripple</span>
            </div>
        </div>
        <div class="container-fluid login-main">
            <div class="container">
                <div class="row" style="margin-top: 1.5em">
                    <div class="alert alert-info" role="alert">
                        <p>
                            Ripple TradeRippleRipple
                        </p>
                    </div>
                </div>
                <div class="lr-box">
                    <el-tabs type="border-card" stretch>
                        <el-tab-pane label="">
                            <in-login></in-login>
                        </el-tab-pane>
                        <el-tab-pane>
                            <span slot="label"></span>
                            <in-reg></in-reg>
                        </el-tab-pane>
                    </el-tabs>
                </div>
            </div>
        </div>
        <Footer></Footer>
    </div>
</template>

<script>
import Footer from './Footer';
import InLogin from './InLogin';
import InReg from './InReg';
// import CreateAddress from './CreateAddress';
export default {
    name: 'Login',
    data () {
        return {
            currentTab: 'in-reg',
            tabs: ['in-login', 'in-reg'],
            activeName: 'ses',
            tabPosition: 'top'
        };
    },
    methods: {

    },
    computed: {
        currentTabComponent: function () {
            return this.currentTab.toLowerCase();
        }
    },
    components: {
        Footer,
        'in-login': InLogin,
        'in-reg': InReg
        // 'create-address': CreateAddress
    }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.top-bg{
    height: 10rem;
    background: #346AA9;
}
.top-box{
    margin: 1.5% auto auto auto;
    text-align: center;
}
.top-title{
    color: #fff;
    font-size: 3em;
    letter-spacing: 0.1em;
}
.alert-info p{
    text-indent: 2em;
}
.lr-box{
    /*width: 45%;*/
    /*min-height: 40em !important;*/
    margin: auto auto 3% auto;
}
</style>



